* {
	box-sizing: border-box;
}
body {
	margin: 0;
	height: 150px;
}
.wp-embed {
	display: flex;
	color: #333;
	width: 100%;
	height: 100%;
	overflow: hidden;
	border: 1px solid #ccc;
}
.wp-embed-featured-image {
	width: 40%;
	pointer-events: none;
}
.wp-embed-featured-image img {
	width: 100%;
    object-fit: cover;
    height: 100%;
}
.wp-embed-heading {
	font-family: "Noto Sans JP",sans-serif;
	display: flex;
    align-items: center;
	width: 60%;
	padding: 0 10px;
	background: #fff;
}
